uch to one of the faction’s disapproval. Illegal extraction operations also emerged, but only for the right price. The monarch’s stash of XYA slowly grew with each passing day, and the regent felt prepared for any conflict that would arise. ")])])]),_c('div',{staticClass:"container cnt-finished left"},[_c('div',{staticClass:"content"},[_c('h2',{staticClass:"text-2xl lg:text-4xl"},[_vm._v("Sir Reginald (DISCORD BOT)")]),_c('br'),_c('p',[_vm._v(" Regent Carroway officially appointed Sir Reginald his butler, who had served the Carroway family for his entire lifetime. Carroway entrusted his loyal butler with all his secrets, handing him the keys to the kingdom of GameFi Crossing. ")])])]),_c('div',{staticClass:"container cnt-finished right"},[_c('div',{staticClass:"content"},[_c('h2',{staticClass:"text-2xl md:text-4xl"},[_vm._v("The World's Revival (APP)")]),_c('br'),_c('p',[_vm._v(" Regent Carroway was determined to start a new era of power and domination for the Freyfolk. Noticing the natural power emanating from the world of GameFi Crossing, the regent decided that XYA was the key to understanding these unknown forces and obtaining balance. The settlement was rebuilt and reborn, as the Freyfolk integrated these crystals into their machines, structures and everyday lives. ")])])]),_c('div',{staticClass:"container cnt-finished left"},[_c('div',{staticClass:"content"},[_c('h2',{staticClass:"text-2xl md:text-4xl"},[_vm._v("Addictive hazards (CASINO)")]),_c('br'),_c('p',[_vm._v(" Opportunists and bandits alike sought out to grow their stashes of XYA, relishing in the power and chaos. Those that wanted this done quickly resulted in wagers and duels ‒ often to the death. High stake games like these were often done in secrecy, away from any involvement from the monarch. An underground corporation named ‘Luderion’ was formed to host these games in secrecy. ")])])]),_c('div',{staticClass:"container cnt-finished right"},[_c('div',{staticClass:"content"},[_c('h2',{staticClass:"t